@use '../inc/vars' as *;
.career-set {
  background-color: $text-light;
  
  .container {
    display: flex;
    flex-direction: column;
    gap: 120px;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      gap: 50px;
    }
    @media(max-width: $xs){
    
    }
    
  }
  &__filters {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      gap: 10px;
    }
    @media(max-width: $xs){
    
    }
    
  }
  &__filters-el {
    min-width: 0;
    &.active {
      background-color: $text;
      color: $text-light;
    }
  }
  .content-row {
    @extend .row !optional;
    overflow: hidden;
    padding-bottom: 2px;
    row-gap: 30px;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
      row-gap: 20px;
    }
    
    &--products {
      margin-left: -80px;
      margin-right: -80px;
      row-gap: 160px;
      @media(max-width: $xl){
        margin-left: -40px;
        margin-right: -40px;
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        row-gap: 60px;
        margin-left: -20px;
        margin-right: -20px;
      }
      @media(max-width: $sm){
      
      }
      @media(max-width: $xs){
      
      }
      
      &--curve-borders {
        & > * {
          &:after {
            display: none;
          }
          &:nth-of-type(4n + 1), &:nth-of-type(4n + 3) {
            @include vertical-borders-single(-160px, 0px, -160px);
          }
          &:nth-of-type(3n) {
            &:after {
              display: none;
            }
          }
        }
      }
    }
  }
  &__body-el {
    @extend .col-xl-3, .col-lg-4, .col-md-6, .col-sm-6, .col-12 !optional;
    
    overflow: hidden;
  }
  .career-card {

  }
}
.product-set__body-el {
  position: relative;
  @extend .col-lg-4, .col-md-6, .col-sm-6 !optional;
  padding-right: 80px !important;
  padding-left: 80px !important;
  
  @include vertical-borders-single(0px, 0px, 0px);
  @media(max-width: $xl){
    padding-right: 40px !important;
    padding-left: 40px !important;
  }
  @media(max-width: $lg){
  
  }
  @media(max-width: $md){
    padding-right: 20px !important;
    padding-left: 20px !important;
  }
  @media(max-width: $sm){
  
  }
  @media(max-width: $xs){
  
  }
  
}
