  margin-left: auto;
  margin-right: auto;
  @media(max-width: $xl){
  
  }
  @media(max-width: $lg){
  
  }
  @media(max-width: $md){
    font-size: 12px;
  }
  @media(max-width: $sm){
  
  }
  @media(max-width: $xs){
  
  }
  
  &__el {
    padding-left: 17px;
    padding-right: 17px;
    height: 55px;
    min-width: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 15px;
    color: $text;
    border: 1px solid transparent;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      height: 35px;
      min-width: 35px;
      border-radius: 8px;
      padding-left: 8px;
      padding-right: 8px;
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
    
    }
    
    &--bordered, &.active {
      border-color: $text;
      
    }
    &:hover {
      text-decoration: underline;
    }
    &.active {
      //min-width: 55px;
    }
  }

  .wp-pagenavi {
    display: flex;
    justify-content: space-between;
    gap: 5px;
    margin-left: auto;
    margin-right: auto;
  }
  .page-numbers {
    padding-left: 17px;
    padding-right: 17px;
    height: 55px;
    min-width: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 15px;
    color: $text;
    border: 1px solid transparent;
    cursor: pointer;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      height: 35px;
      min-width: 35px;
      border-radius: 8px;
      padding-left: 8px;
      padding-right: 8px;
    }
    &.current, &.prev, &.next {
      border-color: $text;
    }
    &:hover {
      text-decoration: underline;
    }
  }
}



